.off {
  display: none;
}

.theme {
  background-color: #E1E8EE;
  min-height: 100vh;
}

.formStructor {
  background-color: #222;
  border-radius: 0 0 15px 15px;
  height: 550px;
  width: 320px;
  position: relative;
  overflow: hidden;

  &::after {
    content: "";
    opacity: .8;
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: -79px;
    background-size: 481px;
    background-image: url('../../images/coin1.jpg');
/*    background-image: url('../public/coin.jpg');*/
  }

  .signup {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 65%;
    z-index: 5;
    transition: all .3s ease;

    &.slideUp {
      top: 5%;
      transform: translate(-50%, 0%);
      transition: all .3s ease;

      .formHolder,
      .submitBtn {
        opacity: 0;
        visibility: hidden;
      }

      .formTitle {
        font-size: 1em;
        cursor: pointer;

        span {
          margin-right: 5px;
          opacity: 1;
          visibility: visible;
          transition: all .3s ease;
        }
      }
    }

    .formTitle {
      color: #fff;
      font-size: 21px;
      text-align: center;

      span {
        color: rgba(0, 0, 0, 0.4);
        opacity: 0;
        visibility: hidden;
        transition: all .3s ease;
      }
    }

    .formHolder {
      border-radius: 15px;
      background-color: #fff;
      overflow: hidden;
      margin-top: 50px;
      opacity: 1;
      visibility: visible;
      transition: all .3s ease;

      .input {
        border: 0;
        outline: none;
        box-shadow: none;
        display: block;
        height: 30px;
        line-height: 30px;
        padding: 8px 15px;
        border-bottom: 1px solid #eee;
        width: 100%;
        font-size: 12px;

        &:last-child {
          border-bottom: 0;
        }

        &::-webkit-input-placeholder {
          color: rgba(0, 0, 0, 0.4);
        }
      }
    }

    .submitBtn {
      background-color: rgba(0, 0, 0, 0.4);
      color: rgba(256, 256, 256, 0.7);
      border: 0;
      border-radius: 15px;
      display: flex;
      justify-content: center;
      margin: 15px auto;
      padding: 15px 45px;
      width: 100%;
      font-size: 13px;
      font-weight: bold;
      cursor: pointer;
      opacity: 1;
      visibility: visible;
      transition: all .3s ease;

      &:hover {
        transition: all .3s ease;
        background-color: rgba(0, 0, 0, 0.8);
      }
    }
  }

  .login {
    position: absolute;
    top: 20%;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #fff;
    z-index: 5;
    transition: all .3s ease;

    &::before {
      content: '';
      position: absolute;
      left: 50%;
      top: -20px;
      transform: translate(-50%, 0);
      background-color: #fff;
      width: 200%;
      height: 250px;
      border-radius: 50%;
      z-index: 4;
      transition: all .3s ease;
    }

    .center {
      position: absolute;
      top: calc(50% - 10%);
      left: 50%;
      transform: translate(-50%, -50%);
      width: 65%;
      z-index: 5;
      transition: all .3s ease;

      .formTitle {
        color: #000;
        font-size: 1.7em;
        text-align: center;

        span {
          color: rgba(0, 0, 0, 0.4);
          opacity: 0;
          visibility: hidden;
          transition: all .3s ease;
        }
      }

      .formHolder {
        border-radius: 15px;
        background-color: #fff;
        border: 1px solid #eee;
        overflow: hidden;
        margin-top: 50px;
        opacity: 1;
        visibility: visible;
        transition: all .3s ease;

        .input {
          border: 0;
          outline: none;
          box-shadow: none;
          display: block;
          height: 30px;
          line-height: 30px;
          padding: 8px 15px;
          border-bottom: 1px solid #eee;
          width: 100%;
          font-size: 12px;

          &:last-child {
            border-bottom: 0;
          }

          &::-webkit-input-placeholder {
            color: rgba(0, 0, 0, 0.4);
          }
        }
      }

      .submitBtn {
        background-color: #6B92A4;
        color: rgba(256, 256, 256, 0.7);
        border: 0;
        border-radius: 15px;
        display: block;
        margin: 15px auto;
        padding: 15px 45px;
        width: 100%;
        font-size: 13px;
        font-weight: bold;
        cursor: pointer;
        opacity: 1;
        visibility: visible;
        transition: all .3s ease;

        &:hover {
          transition: all .3s ease;
          background-color: rgba(0, 0, 0, 0.8);
        }
      }
    }

    &.slideUp {
      top: 90%;
      transition: all .3s ease;

      .center {
        top: 10%;
        transform: translate(-50%, 0%);
        transition: all .3s ease;

        .formHolder,
        .submitBtn {
          opacity: 0;
          visibility: hidden;
          transition: all .3s ease;
        }

        .formTitle {
         font-size: 15px;
          margin: 0;
          padding: 0;
          cursor: pointer;
          transition: ease-in .3s ease;

          span {
            margin-right: 5px;
            opacity: 1;
            visibility: visible;
            transition: all .3s ease;
          }
        }
      }
    }
  }
}
